import React from 'react';

const Toolbar = props => (

    <div className="rocket__toolbar" { ...props } >{ props.children }</div>
)

const Item = props => {

    let { active, ...otherProps } = props;

    let className = 'rocket__toolbar__item';

    if ( active ) {

        className += ' rocket__toolbar__item--active';
    }

    return <span className={ className } { ...otherProps } >{ props.children }</span>
}

Toolbar.Item = Item;

export {

    Toolbar
}